            <!-- <flex-column> -->
            <flex-column-body scroll-parent>
                <fluro-infinite-scroll :perPage="11" :startPage="4" :items="sorted">
                    <template slot-scope="props">

                        <masonry>
                               
                                <brick  :portrait="photo.height > photo.width" v-for="photo in props.page">
                                    <v-flex d-flex @click="view(photo)" style="padding:2px; height:100%; width:100%;">
                                        <photo-thumbnail style="width:100%; height:100%;" :image="photo" />
                                    </v-flex>
