                            </masonry>


                        <!-- <v-container pa-1 fluid grid-list-sm> -->
                            <!-- <v-layout row wrap> -->
                                <!-- <v-flex d-flex @click="view(item)" xs12 sm4 md3 lg2 v-for="item in props.page"> -->
                                    
                                    <!-- <photo-thumbnail :image="item" />
                                    
                                </v-flex>
                            </v-layout>
