<script>
/////////////////////////////////////////////////////

import PhotoThumbnail from '@/components/photos/PhotoThumbnail.vue';
import { FluroInfiniteScroll } from 'fluro-vue-ui'
/////////////////////////////////////////////////////

import BreadcrumbMixin from '@/mixins/BreadcrumbMixin'
import PhotoBrowseMixin from '@/mixins/PhotoBrowseMixin'

export default {
    mixins: [BreadcrumbMixin, PhotoBrowseMixin],
    components: {
        FluroInfiniteScroll,
        PhotoThumbnail,
    },
    data() {
        return {
            loading: true,
        }
    },
    computed: {
        routeKey() {
            return this.$route.name.split('.')[0];
        }
    },
    asyncComputed: {
        sorted: {
            default: [],
            get() {
                var self = this;

                self.loading = true;
                return new Promise(function(resolve, reject) {

                    self.$fluro.api.get('/content/photo', {
                        params: {
                            sort: '-created',
                            fields: [
                                'title',
                                'managedAuthor',
                                'created',
                                'data.photographer',
                                'data.event',
                                'width',
                                'colors',
                                'height',
                            ],
                            limit: 1000,
                        }
                    }).then(function(res) {


                        resolve(res.data);
                        self.loading = false;
                    }, function(err) {
                        reject(err)
                        self.loading = false;
                    });
                })
            }
        },

    },
}
</script>
