<template>
    <!-- Aspect {{aspectRatio}} {{calculatedWidth}}x{{calculatedHeight}} -->
    <div class="photo-thumbnail" :class="classes" :style="style">
        <div class="thumb-top">
            <div class="thumb-left">
                
                    {{title}}
                    <div class="small text-muted">{{date}}</div>
                
            </div>
            <div class="thumb-right">
                <v-btn dark icon :color="selected ? 'primary' : ''" @click.stop="toggleSelection()">
                    <font-awesome-icon :icon="selectionIcon" />
                </v-btn>
            </div>
        </div>
        <fluro-image cover :spinner="true" :imageWidth="imageWidth" :imageHeight="imageHeight" :width="width" :height="height" :item="image" @load="onLoad" @error="onError"></fluro-image>
        <div class="shader"></div>
        <div class="thumb-bottom">
            <div class="thumb-left">
                
                    <template v-if="photographer" >
                        <a @click.stop="viewPhotographer()">
                            <fluro-avatar :id="photographer._id" left :type="photographer.style"></fluro-avatar>
                        </a>
                        {{photographer.title}}
                    </template>
                
            </div>
            <div class="thumb-right">
                <v-btn dark icon @click.stop="download()">
                    <font-awesome-icon :icon="['fas', 'cloud-download']" />
                </v-btn>
                <v-btn dark icon @click.stop="togglePin()" v-if="user">
                    <font-awesome-icon :icon="pinIcon" v-bind="{[`spin`]: pinning}" />
                </v-btn>
            </div>
        </div>
    </div>
    <!-- </pre> -->
</template>
<script>
import _ from 'lodash';
import PinnableMixin from '@/mixins/PinnableMixin';


export default {

    props: {
        image: { type: Object },
        width: { type: Number },
        height: { type: Number },
        defaultColor: {
            type: String,
            default: '#ebeef0',
        }
    },
    mixins: [PinnableMixin],
    data() {
        return {
            loading: true,
        }
    },
    computed: {
        selectionIcon() {
            if (this.selected) {
                return ['fas', 'check'];
            } else {
                return ['far', 'square'];
            }
        },
        calculatedWidth() {
            var self = this;
            return self.width ? self.width : 320;
        },
        calculatedHeight() {
            var self = this;
            if (self.aspectRatio) {
                return (self.calculatedWidth) * self.aspectRatio
            } else {
                return self.height ? self.height : self.calculatedWidth;
            }
        },
        imageWidth() {

            return this.calculatedWidth ? this.calculatedWidth : 320;
        },
        imageHeight() {

            return this.calculatedHeight ? this.calculatedHeight : 320;
        },
        classes() {
            var array = [];

            if (this.selected) {
                array.push('selected');
            }
            return array;
        },
        selected() {
            return this.$selection.isSelected(this.image);
        },
        itemID() {
            return this.$fluro.utils.getStringID(this.image);
        },
        style() {


            // console.log('STYLE', this.image);
            return {


                backgroundColor: _.get(this.image, 'colors[0]') || this.defaultColor
            }
        },
        title() {
            return _.get(this.image, 'data.event.title') || _.get(this, 'image.title');
        },
        date() {
            var date = _.get(this, 'image.created');
            if (!date) {
                return;
            }
            return this.$filters.formatDate(date, 'D MMM YYYY');
        },
        photographer() {
            var image = this.image;

            var photographer = _.get(image, 'data.photographer');
            var uploader = _.get(image, 'managedAuthor');

            if (photographer && photographer.title) {
                return {
                    _id: photographer._id,
                    style: 'contact',
                    title: photographer.title
                };
            }

            if (uploader && uploader.title) {
                return {
                    _id: uploader._id,
                    style: 'persona',
                    title: uploader.title
                };
            }
        },
        smallUrl() {
            return this.$fluro.asset.imageUrl(this.image, 50);
        },
        url() {
            return this.$fluro.asset.imageUrl(this.image, 320);
        },
        aspectRatio() {
            var image = this.image;
            if (!image) {
                return;
            }
            return image.height / image.width;
        },
    },
    methods: {
        toggleSelection() {

            var selectItem = {
                title: this.image.title,
                _id: this.image._id,
                definition: 'photo',
                _type: 'image',
            }
            this.$selection.toggle(selectItem)
        },
        viewPhotographer() {
            var id = this.photographer._id;
            if (this.photographer.style == 'contact') {
                this.$router.push({
                    name: 'photographer',
                    params: {
                        id: id,
                    }
                })
            }
        },
        download() {
            if (!this.image) {
                return;
            }
            var url = this.$fluro.asset.downloadUrl(this.image, { filename: this.image.filename });
            window.open(url);

        },
        onLoad() {
            this.loading = false;
        },
        onError() {
            this.loading = false;
        },
    }
}
</script>
<style scoped lang="scss">
