        background: rgba(#000, 0);
        // transition: background 0.3s;
    }



    .thumb-top {
        top: 0;
    }

    .thumb-bottom {
        bottom: 0;
    }

    .thumb-left {
        flex: 1;
        left: 0;
        min-height: 1px;
    }

    .thumb-right {
        // max-width: 96px;
        flex: none;
        text-align: right;
    }

    .v-btn {
        margin: 0;
    }

    .avatar {
        margin: 3px;
        width: 30px;

    }



    &:hover {
        &>.shader {
