export default {
    methods: {
        view(photo) {
            var photoID = this.$fluro.utils.getStringID(photo);
            this.$router.push({ query: { photo: photoID } });
        },
        previousPhoto(id) {
            var self = this;

            var photoArray = self.sorted || [];
            var beforeIndex = _.findIndex(photoArray, { _id: id });
            var index = beforeIndex;
            
             index = index -1;
            
            if(index < 0) {
                index = photoArray.length-1;
            }

            var target = photoArray[index];
            console.log('PREVIOUS', id, beforeIndex, index, target, photoArray)
            if (target) {
                var targetID = self.$fluro.utils.getStringID(target);
                self.$router.replace({
                    query: {
                        photo: targetID,
                    }
                })
            }
        },
        nextPhoto(id) {

            var self = this;

             var photoArray = self.sorted || [];
            var beforeIndex = _.findIndex(photoArray, { _id: id });
            var index = beforeIndex;



           

            //Increase
            index = index +1;
            
            if(index > photoArray.length-1) {
                index = 0;
            }

             console.log('NEXT', id, beforeIndex, index, photoArray)

            var target = photoArray[index];
            if (target) {
                var targetID = self.$fluro.utils.getStringID(target);
                self.$router.replace({
                    query: {
                        photo: targetID,
                    }
                })
            }
        },
    },
    activated() {
        console.log('PHOTO ACTIVATED')
        var self = this;
        self.$fluro.addEventListener('previousPhoto', self.previousPhoto)
        self.$fluro.addEventListener('nextPhoto', self.nextPhoto)
    },
    deactivated() {
        console.log('PHOTO DEACTIVATED')
        var self = this;
        self.$fluro.removeEventListener('previousPhoto', self.previousPhoto)
        self.$fluro.removeEventListener('nextPhoto', self.nextPhoto)
    },
}